import { common } from "../data/common";

export default function priceFormat(value: number) : string {
    if(typeof value === 'undefined' || value === null)
        return "";

    if(typeof value === 'string')
        value = parseFloat(value);

    const currency = common.get('currency');
    if(!currency) return value.toString();

    return priceFormatNoCurrency(value) + ` ${currency.symbol}`;
}

export function priceFormatNoCurrency(value: number) : string {
    if(typeof value === 'undefined' || value === null)
        return "";

    if(typeof value === 'string')
        value = parseFloat(value);

    const currency = common.get('currency');
    if(!currency) return value.toString();
    
    return value.toFixed(2).replace('.', currency.separator);
}